#products-page{
  background-image: url("../components-images/hospital-img.jpg");
  background-repeat: round;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  }

  
  
#products-page-b{
  color: white;
  background-color: black;
}

.cart-prods-bought{
  display: flex;
  width: 50%;
  justify-content: center;
  align-items: center;
  height: 10vh;
  gap: 50px;
  background-color: #6f0f9a;
  color: white;
  border-radius: 20px;
}

@media (max-width: 700px) {
    .cart-prods-bought{
      display: flex;
      width: 100%;
      justify-content: space-around;
      align-items: center;
      height: 10vh;
      gap: 0px;
      background-color: #6f0f9a;
      color: white;
      border-radius: 20px;
      font-size: x-small;
     }
   }

.delete-single-prod{
  cursor: pointer;
  padding: 5px;
  border-color: transparent;
  border-radius: 5px;
  font-size: 1em;
  color: red;
}

.delete-single-prod:hover{
  background-color: red;
  color: white;
  transition: 1s;
}

#home-route{
  cursor: pointer;
  display: flex;
  gap: 5px;
  text-decoration: none;
}

#clean-cart{
  background-color:mediumpurple;
  color: white;
  padding: 10px;
  cursor: pointer;
  border-radius: 15px;
}

#clean-cart:hover{
  background-color: indigo;
  transition: 2s;
}

#check-final{
  background-color: darkgray;
  color: #73ff73;
  padding: 10px;
  margin: 5px;
  border-radius: 15px;
}

#payment-check-container{
  display: flex;
  width: 100%;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

@media screen and (max-width: 700px) {
  #payment-check-container{
    display: flex;
    width: 100%;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    max-width: 30em;
  }
  
}